<template>

    <v-card class="computer-card-1">
      <v-card-text style="position: relative;">
        <p class="text-h6 text--primary">
            <v-icon style="font-size: 18px;margin-bottom:4px;margin-right: 6px;">mdi-desktop-classic</v-icon>
            FXCM 

            <a-input-number v-model:value="money" @change="changeMoney" :min="10" :max="10000" style="margin-left:32px;" />
            <a-tag color="#55acee" style="margin-left: 22px">
                <template #icon>
                    <v-icon icon="mdi-bird"></v-icon>
                </template>
                <b style="font-size: 12px;padding-left: 8px;">{{ rate }}</b>
            </a-tag>
        </p>


      </v-card-text>

      <div style="padding: 0px 16px;">

        <a-table :columns="columns" :data-source="data" :pagination="false" size="small">
            <template #headerCell="{ column }">
                <template v-if="column.key === 'close'">
                    <strong>{{ column.title }}</strong>
                    <v-icon color="#FF5722" icon="mdi-close-outline"></v-icon>
                </template>
                <template v-if="column.key === 'priceStart'">
                    <strong style="color: #8BC34A;">{{ column.title }}</strong>
                </template>
                <template v-if="column.key === 'priceOver'">
                    <strong style="color: #FF5722;">{{ column.title }}</strong>
                </template>


                <template v-if="column.key === 'count'">
                    <strong>{{ column.title }}</strong>
                    <v-icon color="#FFC107" icon="mdi-cards"></v-icon>
                </template>

                
                <template v-if="column.key === 'number'">
                    <strong>{{ column.title }}</strong>
                    <v-icon color="#8BC34A" icon="mdi-database"></v-icon>
                </template>

                <template v-if="column.key === 'profit'">
                    <strong>{{ column.title }}</strong>
                    <v-icon color="#00BCD4" icon="mdi-currency-usd"></v-icon>
                </template>
                <template v-if="column.key === 'computer'">
                    <strong>{{ column.title }}</strong>
                    <v-icon color="#00BCD4" icon="mdi-desktop-classic"></v-icon>
                </template>
            </template>

            <template #bodyCell="{ column, record }">
                <template v-if="column.key === 'type'">
                    <b>{{ record.title }}</b>
                </template>
                <template v-if="column.key === 'priceStart' && record.type!='line'">
                    <a-input-number style="width:130px;" v-model:value="record.valuePriceStart" :precision="record._precision" />
                </template>
                <template v-if="column.key === 'priceOver' && record.type!='line'">
                    <a-input-number style="width:130px;" v-model:value="record.valuePriceOver" :precision="record._precision" />
                </template>

                <template v-if="column.key === 'computer'">
                    <v-btn v-if="record.type!='line'" @click="(val)=> this.valueChange(record)" density="compact" color="#00BCD4" style="color:#fff" icon="mdi-desktop-classic"></v-btn>
                    <v-icon v-if="record.direction=='buy'" color="#8BC34A" icon="mdi-arrow-up-thin" size="28"></v-icon>
                    <v-icon v-if="record.direction=='sell'" color="#FF5722" icon="mdi-arrow-down-thin" size="28"></v-icon>
                </template> 

            </template>
        </a-table>




      </div>
    </v-card>

</template>
<script>
import { defineComponent, h } from 'vue'
import BigNumber from "bignumber.js";


export default defineComponent({
    data: (_this) => ({ 

        money:20,   // 
        rate:7,     // 

        // 
        columns:[
            {
                title: 'Type',
                dataIndex: 'type',
                key: 'type',
                width:120
            },
            {
                title: '',
                dataIndex: 'priceStart',
                key: 'priceStart',
                width:160
            },
            {
                title: '',
                dataIndex: 'priceOver',
                key: 'priceOver',
                width:160
            },
            {
                title: '',
                dataIndex: 'count',
                key: 'count',
                width:130
            },
            {
                title: '',
                dataIndex: 'number',
                key: 'number',
                width:130
            },
            {
                title: '',
                dataIndex: 'profit',
                key: 'profit',
                width:160
            },

            {
                title: 'Computer',
                dataIndex: 'computer',
                key: 'computer',
                width:130

            },
            {
                title: '',
                dataIndex: 'remark',
                key: 'remark',
          

            },
        ],

        // 
        data:[
            {
                type: 'NA100',    // 
                title:"100",         // 
                _precision:0,   //  
                valuePriceStart: 15010,   // 
                valuePriceOver: 15000,   //  
                remark:"1 1 1USD",  // 
            },
            {
                type: 'SPX500',    // 
                title:"500",         // 
                _precision:0,   //  
                valuePriceStart: 4480,   // 
                valuePriceOver: 4470,   //  
                remark:"1 1 1USD",  // 
            },
            {
                type: 'US30',    // 
                title:"",         // 
                _precision:0,   //  
                valuePriceStart: 35210,   // 
                valuePriceOver: 35200,   //  
                remark:"1 1 1USD",  // 
            },
            {
                type: 'US2000',    // 
                title:"2000",         // 
                _precision:0,   //  
                valuePriceStart: 1920,   // 
                valuePriceOver: 1910,   //  
                remark:"1 1 1USD",  // 
            },
            { type:"line", remark:"-"},
            {
                type: 'GER40',    // 
                title:"40",         // 
                _precision:0,   //  
                valuePriceStart: 15810,   // 
                valuePriceOver: 15800,   //  
                remark:"1 1 1USD",  // 
            },
            {
                type: 'FH40',    // 
                title:"40",         // 
                _precision:0,   //  
                valuePriceStart: 7210,   // 
                valuePriceOver: 7200,   //  
                remark:"1 1 1USD",  // 
            },
            {
                type: 'UK100',    // 
                title:"100",         // 
                _precision:0,   //  
                valuePriceStart: 7520,   // 
                valuePriceOver: 7500,   //  
                remark:"1 1 1USD ",  // 
            },
            {
                type: 'aus200',    // 
                title:"",         // 
                _precision:0,   //  
                valuePriceStart: 7320,   // 
                valuePriceOver: 7300,   //  
                remark:"1 1 1USD ",  // 
            },
            {
                type: 'jp225',    // 
                title:"",         // 
                _precision:0,   //  
                valuePriceStart: 31787,   // 
                valuePriceOver: 31930,   //  
                remark:"1 1 1USD ",  // 
            },

            { type:"line", remark:"-"},
            {
                type: 'CH50',    // 
                title:"50",         // 
                _precision:0,   //  
                valuePriceStart: 13120,   // 
                valuePriceOver: 13100,   //  
                remark:"1 1 1USD",  // 
            },
            {
                type: 'HK33',    // 
                title:"",         // 
                _precision:0,   //  
                valuePriceStart: 19190,   // 
                valuePriceOver: 19295,   //  
                remark:"1 1 1USD ",  // 
            },
            { type:"line", remark:"-"},

            {
                type: 'XAUUSD',    // 
                title:"",         // 
                _precision:0,   //  
                valuePriceStart: 1950,   // 
                valuePriceOver: 1940,   //  
                remark:"1 1 1USD",  // 
            },
            {
                type: 'XAGUSD',    // 
                title:"",         // 
                _precision:3,   //  
                valuePriceStart: 23.145,   // 
                valuePriceOver: 23.023,   //  
                remark:"1 0.001 1USD",  // 
            },
            { type:"line", remark:"-"},
            {
                type: 'GBPUSD',    // 
                title:"-",         // 
                _precision:5,   //  
                valuePriceStart: 1.27181,   // 
                valuePriceOver: 1.26970,   //  
                remark:"1 0.00001 1USD",  // 
            },
            {
                type: 'EURJPY',    // 
                title:"-",         // 
                _precision:3,   //  
                valuePriceStart: 158.430,   // 
                valuePriceOver: 158.120,   //  
                remark:"1 0.001 1USD ",  // 
            },
            {
                type: 'AUDNZD',    // 
                title:"-",         // 
                _precision:5,   //  
                valuePriceStart: 1.08530,   // 
                valuePriceOver: 1.08365,   //  
                remark:"1 0.00001 1USD",  // 
            },
            {
                type: 'CADCHF',    // 
                title:"-",         // 
                _precision:5,   //  
                valuePriceStart: 0.65178,   // 
                valuePriceOver: 0.65052,   //  
                remark:"1 0.00001 1USD",  // 
            },
        ]


    }),
    created(){
        const _value=localStorage.getItem("page_computer_money")||10
        this.money=_value
    },
    methods:{
        // close change 111
        valueChange(record){
            const {money,rate}=this
            const {type,valuePriceStart,valuePriceOver}=record




            // 100   500   40 40 100  50 
            if( ["NA100","SPX500","US30","US2000", "GER40","FH40","UK100","aus200","CH50","XAUUSD"].includes(type) ){
                var _count= (new BigNumber(valuePriceStart).minus(valuePriceOver)).absoluteValue().toNumber()
                var _profit=new BigNumber(_count).multipliedBy(rate).toNumber()  // 
                
                record.number= new BigNumber(money).dividedBy(_count).toNumber().toFixed(1)   // 

                
                //  | 
                if(valuePriceStart>valuePriceOver){
                    record.direction='buy'   
                    record.profit= new BigNumber(valuePriceStart).plus(_profit).toNumber()   // 
                }else{
                    record.direction='sell'
                    record.profit= new BigNumber(valuePriceStart).minus(_profit).toNumber()  // 
                }
                record.count=_count  // 
            }


            // 225
            if( ["jp225"].includes(type) ){

                var _count= (new BigNumber(valuePriceStart).minus(valuePriceOver)).absoluteValue().toNumber()
                var _profit=new BigNumber(_count).multipliedBy(rate).toNumber()  // 
                
                // record.number= new BigNumber(money).dividedBy(_count).toNumber().toFixed(1)   // 

                
                //  | 
                if(valuePriceStart>valuePriceOver){
                    record.direction='buy'   
                    record.profit= new BigNumber(valuePriceStart).plus(_profit).toNumber()   // 
                }else{
                    record.direction='sell'
                    record.profit= new BigNumber(valuePriceStart).minus(_profit).toNumber()  // 
                }
                record.count=_count  // 

            }


            // 
            
            if( ["HK33"].includes(type) ){

                var _count= (new BigNumber(valuePriceStart).minus(valuePriceOver)).absoluteValue().toNumber()
                var _profit=new BigNumber(_count).multipliedBy(rate).toNumber()  // 
                
                record.number= new BigNumber(money).dividedBy(_count).multipliedBy(10).toNumber().toFixed(1)   // 


                //  | 
                if(valuePriceStart>valuePriceOver){
                    record.direction='buy'   
                    record.profit= new BigNumber(valuePriceStart).plus(_profit).toNumber()   // 
                }else{
                    record.direction='sell'
                    record.profit= new BigNumber(valuePriceStart).minus(_profit).toNumber()  // 
                }
                record.count=_count  // 


            }

            // 
            if( ["XAGUSD"].includes(type) ){
            
            
                var _count= (new BigNumber(valuePriceStart).minus(valuePriceOver)).toNumber()
                var _profit=new BigNumber(_count).multipliedBy(rate).toNumber()  // 
                
                record.number= new BigNumber(money).dividedBy(_count).toNumber().toFixed(1)   // 

                //  | 
                if(valuePriceStart>valuePriceOver){
                    record.direction='buy'   
                    record.profit= new BigNumber(valuePriceStart).plus(_profit).toNumber()   // 
                }else{
                    record.direction='sell'
                    record.profit= new BigNumber(valuePriceStart).minus(_profit).toNumber()  // 
                }
                record.count=_count  // 

            
            }



            // 
            if( ["GBPUSD","AUDNZD","CADCHF"].includes(type) ){
            
            
                var _count= (new BigNumber(valuePriceStart).minus(valuePriceOver)).toNumber()
                var _profit=new BigNumber(_count).multipliedBy(rate).toNumber()  // 
                
                record.number= new BigNumber(money).dividedBy(_count).dividedBy(100000).toNumber().toFixed(1)   // 

                //  | 
                if(valuePriceStart>valuePriceOver){
                    record.direction='buy'   
                    record.profit= new BigNumber(valuePriceStart).plus(_profit).toNumber()   // 
                }else{
                    record.direction='sell'
                    record.profit= new BigNumber(valuePriceStart).minus(_profit).toNumber()  // 
                }
                record.count=_count  // 

            }

            // 
            if( ["EURJPY"].includes(type) ){
            
            
                var _count= (new BigNumber(valuePriceStart).minus(valuePriceOver)).toNumber()
                var _profit=new BigNumber(_count).multipliedBy(rate).toNumber()  // 
                
                record.number= new BigNumber(money).dividedBy(_count).dividedBy(1000).toNumber().toFixed(1)   // 

                //  | 
                if(valuePriceStart>valuePriceOver){
                    record.direction='buy'   
                    record.profit= new BigNumber(valuePriceStart).plus(_profit).toNumber()   // 
                }else{
                    record.direction='sell'
                    record.profit= new BigNumber(valuePriceStart).minus(_profit).toNumber()  // 
                }
                record.count=_count  // 

            }
            
        },
        // 
        changeMoney(value){
            localStorage.setItem("page_computer_money",value)
        }
    }
})
</script>
<style>
.computer-card-1{

}
</style>

  